<template>
  <div class="DynamicCss-box">
    <div class="block">
      <el-row>
        <el-col :span="6">
          <span class="demonstration">调整宽度</span
          ><el-slider v-model="width" @change="change($event)"></el-slider
        ></el-col>
        <el-col :span="6"
          ><span class="demonstration">调整高度</span
          ><el-slider v-model="height"></el-slider
        ></el-col>
        <el-col :span="6"
          ><span class="demonstration">调整圆角</span
          ><el-slider v-model="radius"></el-slider
        ></el-col>
        <el-col :span="6"
          ><span class="demonstration">调整背景颜色</span>
          <el-color-picker v-model="color" size="mini"></el-color-picker>
        </el-col>
        <el-col :span="6"
          ><span class="demonstration">调整背景颜色透明度</span
          ><el-slider v-model="opacity"></el-slider
        ></el-col>
      </el-row>
      <!-- 被控制盒子 -->
      <div class="Controlled-box"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "DynamicCss",
  data() {
    return {
      width: 50,
      height: 15,
      radius: 10,
      color: "#409EFF",
      opacity: 1,
    };
  },
  methods: {
    change($event) {},
  },
};
</script>

<style lang="less" scoped>
.DynamicCss-box {
  width: 1200px;
  margin: 0 auto;

  .block {
    .el-row {
      .el-col {
        margin-right: 40px;
        margin-bottom: 15px;
      }
    }

    .Controlled-box {
      width: 100px;
      height: 100px;
      background: #000;
    }
  }
}
</style>