<template>
  <div class="convert-box">
    <h2>HTML转JS工具</h2>

    <h4>HTML转换为JS</h4>
    <div class="convert">
      <el-input
        class="inlet"
        type="textarea"
        :rows="2"
        placeholder="请输入内容"
        v-model="textareaHtml"
        @input="rechange($event)"
      >
      </el-input
      ><el-input
        class="export"
        type="textarea"
        :rows="2"
        placeholder="等待输入内容"
        v-model="convertTextareaHtml"
      >
      </el-input>
    </div>

    <h4>JS转换为HTML</h4>
    <div class="convert">
      <el-input
        class="inlet"
        type="textarea"
        :rows="2"
        placeholder="请输入内容"
        v-model="textareaJs"
        @input="change($event)"
      >
      </el-input
      ><el-input
        class="export"
        type="textarea"
        :rows="2"
        placeholder="等待输入内容"
        v-model="convertTextareaJs"
      >
      </el-input>
    </div>
  </div>
</template>

<script>
export default {
  name: "Convert",
  // data存放数据
  data() {
    return {
      textareaHtml: "",
      convertTextareaHtml: "",
      textareaJs: "",
      convertTextareaJs: "",
    };
  },
  // methods存放方法
  methods: {
    // HTML 转 JS
    rechange($event) {
      this.$data.convertTextareaHtml =
        'document.writeln("' +
        $event
          .replace(/\\/g, "\\\\")
          .replace(/\\/g, "\\/")
          .replace(/\'/g, "\\'")
          .replace(/\"/g, '\\"')
          .split("\n")
          .join('");\ndocument.writeln("') +
        '");';
    },
    // JS 转 HTML
    change($event) {
      this.$data.convertTextareaJs = $event
        .replace(/document.writeln\("/g, "")
        .replace(/"\);/g, "")
        .replace(/\\\"/g, '"')
        .replace(/\\\'/g, "'")
        .replace(/\\\//g, "/")
        .replace(/\\\\/g, "\\");
    },
  },
};
</script>

<style lang="less" scoped>
.convert-box {
  width: 1200px;
  margin: 0 auto;
  text-align: center;

  .convert {
    display: flex;
  }
}
</style>