// 配置路由的地方
import Vue from "vue";
import VueRouter from "vue-router";

// 使用插件
Vue.use(VueRouter)

// 引入路由组件
import Home from "@/pages/Home/index.vue"
import Convert from '@/pages/Convert/index.vue'
// 二进制转换-测试另一种写法所有注释掉
import Bin2Dec from "@/pages/Bin2Dec/index.vue"
import DynamicCss from "@/pages/DynamicCss/index.vue"
import ImgCompress from "@/pages/ImgCompress/index.vue"
import GetApiList from "@/pages/GetApiList/index.vue"


// 配置路由，需要先暴露一个vue-router类的实例
export default new VueRouter({
    // mode: 'history',  //  VueRouter 使用 HTML5 的 History API 来管理路由,目前有些问题，暂时不用
    // 配置路由
    routes: [
        // 主页
        {
            path: "/Home",  // 访问的根目录
            name: 'home',
            // props: true, // 允许通过路由参数传递props
            component: Home,
            meta: { show: true }
        },
        {
            // html-js互相转换
            path: "/convert",  // 在该目录下访问的是 /convert/*****
            name: 'Convert',
            component: Convert,
            meta: { show: true }
        },
        {
            // 二进制
            path: "/bin2dec",
            name: 'Bin2dec',
            component: Bin2Dec,
            // components: () => import('@/pages/Bin2Dec/Bin2Dec'),
            meta: {
                title: '二进制转换'
            }
        },
        // 动态调整样式
        {
            path: "/dynamicCss",
            name: 'dynamicCss',
            component: DynamicCss,
            meta: { show: false }
        },
        // 图片压缩
        {
            path: "/imgCompress",
            name: 'imgCompress',
            component: ImgCompress,
            meta: { show: false }
        },
        // 请求各种接口地址
        {
            path: "/getApiList",
            name: 'getApiList',
            component: GetApiList,
            meta: { show: false }
        },
        {
            // 重定向，在项目跑起来的时候，访问/，立马重定向到主页
            path: "*",
            redirect: '/Home'
        },
    ]
})