<template>
  <div class="GetApiList-box">
    <div class="DouYinVideo">
      <el-button @click="newList">点击请求发送请求</el-button>
      <div class="List-box">{{ douYinList.msg }}</div>
      <br />
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex"; // 引入 mapState 方法
export default {
  name: "GetApiList",
  data() {
    return {};
  },
  methods: {
    newList() {
      // 获取数据渲染到组件 第一步
      this.$store.dispatch("getDouYinList");
    },
  },
  mounted() {
    this.newList(); // 将该方法放到生命周期中，为了在页面刚加载时调用api
  },

  // 获取数据渲染到组件 第二步 此时已经可以将获取到的数据渲染到组件上了
  computed: {
    ...mapState({
      douYinList: (state) => state.getApiList.douYinList,
    }),
  },
};
</script>

<style lang="less" scoped>
.GetApiList-box {
  width: 1200px;
  margin: 0 auto;
  .List-box {
    font-size: 16px;
    line-height: 32px;
    text-indent: 2rem;
    margin-top: 30px;
  }
}
</style>