<template>
  <div class="bin2dec-box">
    <h4>进制转换</h4>
    <div class="bin2dec-list" @change="change($event)">
      <el-radio v-model="radio" label="2">2进制</el-radio>
      <el-radio v-model="radio" label="8">8进制</el-radio>
      <el-radio v-model="radio" label="10">10进制</el-radio>
      <el-radio v-model="radio" label="16">16进制</el-radio>
      <el-radio v-model="radio" label="32">32进制</el-radio>
    </div>
    <el-input
      v-model="input"
      @input="change($event)"
      placeholder="等待输入"
    ></el-input>

    <el-input
      v-model="transform"
      placeholder="等待输出"
      :disabled="true"
    ></el-input>
  </div>
</template>

<script>
export default {
  name: "Bin2dec",
  data() {
    return {
      // 输入框的文本值
      input: "",
      // 输出框的文本值
      transform: "",
      // 进制,默认为2进制
      radio: "2",
    };
  },
  methods: {
    change($event) {
      // 为了动态更改转换后的值，这里的方法必须要在勾选转换进制选项时触发
      // 转化的进制
      if ($event.type == "change") {
        this.radio = $event.target.value;
        // 切换进制时，让输入框为空
        this.input = "";
      }
      this.transform = parseInt($event).toString(this.radio);
      // 补全 补充成4位展示
      let index = this.transform.length;
      if (index < 4) {
        for (let i = 0; i < 4 - index; i++) {
          if (this.transform !== "NaN") {
            this.transform = "0" + this.transform;
          }
        }
      }
    },
  },
};
</script>

<style lang="less" scoped>
.bin2dec-box {
  width: 1200px;
  margin: 0 auto;
  .bin2dec-list {
    margin: 15px 0;
  }
}
</style>